<template>
  <div class="tabs">
    
    <h1 class="text-center mb-50">Tabs Page</h1>    

    <v-container>
      <v-row>
        <v-col>

          <v-card class="w-100">
            <!-- Options : 
              1. stacked :       
              2. align-tabs :  
              3. color :  
              4. class :  
              5. fixed-tabs :    
              6. center-active :             .
              7. prev-icon & next-icon :        
             -->
            <!-- <v-tabs v-model="tab" stacked align-tabs="left" color="deep-purple-accent-4">  -->

            <v-tabs v-model="tab" class="bg-red" fixed-tabs> 
              <!-- <v-tab>Tab1</v-tab>
              <v-tab>Tab2</v-tab>
              <v-tab>Tab3</v-tab> -->

              <!-- <v-tab v-for="(num) in 4" :key="num" :value="num">Tab{{ num }}</v-tab> -->
              <v-tab v-for="(num) in 4" :key="num" :value="num">
                <v-icon>mdi-home</v-icon> Tab{{ num }}
              </v-tab>
            </v-tabs>
            <v-window v-model="tab" class="mt-5 mb-5">
              <v-window-item v-for="num in 4" :key="num" :value="num">
                <v-card class="pa-5">
                  <v-title>Card{{ num }}</v-title>
                </v-card>
              </v-window-item>
            </v-window>
            
          </v-card>

        </v-col>
      </v-row>

      <v-row class="mt-10">
        <v-col>
          <v-tabs
            v-model="tab1"
            align-tabs="left"
          >
            <v-tab
              v-for="item in items"
              :key="item"
              :text="item"
              :value="item"
            ></v-tab>
          </v-tabs>

          <v-tabs-window v-model="tab1">
            <v-tabs-window-item
              v-for="item in items"
              :key="item"
              :value="item"
            >
              <v-card flat>
                <!--  :text     :
                  v-text :text Vue.js       . 
                  v-text   , :text         . 
                                 . 
                   ,       v-text   . 
                -->
                <v-card-text v-text="text"></v-card-text>
              </v-card>
            </v-tabs-window-item>
          </v-tabs-window>
        </v-col>
      </v-row>

      <v-row>
        <v-col>
          <v-tabs v-model="tab2" align-tabs="left">
            <v-tab v-for="item in items" :key="item" :value="item">{{ item }}</v-tab>
          </v-tabs>

          <v-tabs-window v-model="tab2">
            <v-tabs-window-item v-for="item in items" :key="item" :value="item">
              <v-card flat>
                <!-- <v-card-text>{{ getTabContent(item) }}</v-card-text> -->
                <v-card-text>
                  <v-card-text>
                    <div v-html="getTabContent(item)"></div>
                  </v-card-text>
                </v-card-text>
              </v-card>
            </v-tabs-window-item>
          </v-tabs-window>
        </v-col>
      </v-row>
    </v-container>

  </div>
</template>

<script>
export default {
  name: 'TabsView',

  data() {
    return {
      tab: null,
      tab1: null,
      tab2: null,
      items: [
        'web', 'shopping', 'videos', 'images', 'news',
      ],
      text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.',
      tabContents: {
        web: '<h3>Web</h3>  .',
        shopping: '<h3>Shopping</h3>  .',
        videos: '<h3>Videos</h3>  .',
        images: '<h3>Images</h3>  .',
        news: '<h3>News</h3>  .',
      },
    };
  },
  methods: {
    getTabContent(tabItem) {
      return this.tabContents[tabItem];
    },
  },
};
</script>